.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    gap: 0.5em;
}

.wrapper button {
    /*border-radius: 50%;*/
    padding: 0.8em;
}

.wrapper input {
    /*width: fit-content;*/
    width: 90%;
    padding: 1%;
    font-size: 1.8em;
    text-align: center;
    border-radius: 0.2em;
    border: 1px solid var(--border-color);
}

.btn {
    /*padding: 0.8em;*/
    border-radius: 0.2em;
    position: relative;
}

.arrowRight {
    position: absolute;
    top: 0;
    right: -15px;
    font-size: 1rem;
    width: 30px;
    /*padding: 0.8em 0;*/
    border: 1px solid red;

}
